.main{
  background-image: url(../img/main-bg.webp);
  background-size: auto 100%;
  background-position: right top;
  background-repeat: no-repeat;
  overflow: hidden;
  padding: 0 30px;
  position: relative;
  &__heart{
    position: absolute;
    left: 0;
    top: 18%;
  }
  &__inner{
    margin: 0 auto;
    max-width: 100%;
    width: 1675px;
    min-height: 100vh;
    max-height: 1072px;
    padding: 88px 0 0;
    display: flex;
    justify-content: space-between;
  }
  &__content{
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-transform: uppercase;
    padding-bottom: 120px;
    position: relative;
    z-index: 2;
    transition: all 2s;
  }
  &__img{
    max-width: 50%;
    align-self: flex-end;
    transition-delay: .75s !important;
    img{
      max-width: 100%;
      width: 100%;
    }
  }
  &__price{
    font-weight: 700;
    font-size: 128px;
    line-height: 142%;
    color: $primary;
    white-space: nowrap;
    width: calc(100% + 70px);
  }
  &__desc{
    font-weight: 300;
    font-size: 50px;
    line-height: 142%;
    max-width: 730px;
    transition-delay: .5s !important;
  }
  &__nav{
    max-width: 100%;
    width: 1735px;
    padding: 0 30px;
    left: 50%;
    bottom: 82px;
    transform: translateX(-50%);
    position: absolute;
    display: flex;
    align-items: center;
    z-index: 3;
  }
  .swiper{
    &-button{
      &-prev,
      &-next{
        position: static;
        margin-top: 0;
        width: unset;
        height: unset;
      }
    }
    &-pagination{
      position: static;
      width: unset;
      margin: 0 14px;
      font-size: 14px;
      line-height: 19px;
      color: #3E475B;
      font-variant-numeric: tabular-nums;
      -webkit-font-feature-settings: "tnum";
      font-feature-settings: "tnum";
      &-current{
        font-size: 24px;
        line-height: 134%;
        font-weight: 700;
      }
    }
    &-slide{
      &-active,
      &-active~.swiper-slide{
        .animated-item{
          transform: translateX(2000px);
          transition: all 1s;
        }
      }
    }
  }

  ._anim{
    .animated-item{
      transform: translateX(0);
    }
  }
}

.donation{
  background: #F2F4FA;
  padding: 120px 116px 130px;
  position: relative;
  overflow: hidden;
  &__content{
    width: 54.51%;
    margin-left: auto;
  }
  &__img{
    height: 100%;
    &-wrap{
      position: absolute;
      left: 0;
      top: 0;
      height: 100%;
      svg{
        position: absolute;
        left: 0;
        bottom: 20%;
        height: 28%;
        width: auto;
      }
    }
  }
  &__desc,
  &__form{
    position: relative;
    z-index: 2;
  }
  &__desc{
    font-size: 30px;
    line-height: 133%;
    b{
      font-weight: 700;
    }
  }
  &__form{
    margin-top: 20px;
    background: #FFFFFF;
    box-shadow: 0px 4px 15px #D7EFFB;
    border-radius: 10px;
    max-width: 747px;
    padding: 34px 55px 49px;
  }
  &__title{
    font-weight: 700;
    font-size: 33px;
    line-height: 142%;
    color: $primary;
    text-transform: uppercase;
  }
  &__radios{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 20px;
    margin-top: 21px;
  }
  &__radio{
    position: relative;
    height: 38px;
    input{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 2;
      cursor: pointer;
      opacity: 0;
      &:checked+label{
        background: $primary;
        color: #fff;
        border-color: $primary;
      }
    }
    label{
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      border: 1px solid #828282;
      color: #4F4F4F;
      font-size: 18px;
      line-height: 142%;
      text-transform: uppercase;
    }
    &--text{
      input{
        opacity: 1;
        text-align: center;
        font-size: 18px;
        line-height: 142%;
        color: #4F4F4F;
        text-transform: uppercase;
        padding: 0 5px;
        &::placeholder{
          color: #4F4F4F;
        }
      }
    }
    & ._active{
      color: #fff;
      border-color: $primary;
      background-color: $primary;
      &::placeholder{
        color: #fff;
      }
    }
  }
  &__inputs{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 17px;
    margin-top: 39px;
  }
  &__check{
    margin-top: 20px;
  }
  &__btn{
    margin-top: 25px;
    height: 46px;
    width: 100%;
    font-size: 18px;
  }
}

.friends{
  padding: 51px 0 0;
  .container{
    max-width: 1440px;
  }
  &__inner{
    display: grid;
    grid-template-columns: 1.59fr 1fr;
    gap: 10px;
  }
  &__left{
    padding-top: 5.5%;
  }
  &__nums{
    padding: 35px 50px;
    background: #FFFFFF;
    box-shadow: 0px 4px 15px #D7EFFB;
    border-radius: 10px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 30px;
    max-width: 641px;
    margin: 0 auto;
  }
  &__num{
    font-weight: 700;
    font-size: 174px;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 226px;
    border: 2px solid #3E475B;
    border-radius: 10px;
    color: #3E475B;
  }
  &__title{
    font-weight: 700;
    font-size: 24px;
    line-height: 134%;
    text-align: center;
    margin-top: 28px;
    span{
      display: inline-block;
      border-bottom: 3px solid $primary;
    }
  }
  &__img{
    position: relative;
    img{
      width: 100%;
      display: block;
    }
    svg{
      position: absolute;
      height: auto;
      width: 45.2%;
      top: -4%;
      left: 50%;
      transform: translateX(-50%);
    }
  }
}


@media screen and (max-width: 1600px){
  .main{
    &__price{
      font-size: 100px;
    }
    &__desc{
      font-size: 43px;
    }
  }
}


@media screen and (max-width: 1500px){
  .donation{
    padding: 50px 30px;
    &__content{
      width: 65%;
    }
    &__img{
      &-wrap{
        right: 25%;
        left: unset;
      }
    }
    &__desc{
      font-size: 25px;
    }
  }
}


@media screen and (max-width: 1400px){
  .main{
    &__price{
      font-size: 90px;
    }
    &__desc{
      font-size: 40px;
    }
  }
}


@media screen and (max-width: 1024px){
  .main{
    padding: 158px 15px 25px;
    &__heart{
      top: 90px;
      height: 120px;
    }
    &__inner{
      display: block;
      min-height: unset;
      max-height: unset;
    }
    &__content{
      padding: 0;
    }
    &__img{
      max-width: unset;
      width: calc(100% + 30px);
      margin-left: -15px;
      position: relative;
      padding-bottom: 100%;
      img{
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
        height: 100%;
        width: auto;
      }
    }
    &__nav{
      position: static;
      justify-content: center;
      margin-top: 12px;
      transform: unset;
    }
    .swiper{

    }
  }

  .donation{
    padding: 25px 0 0;
    &__content{
      width: 100%;
    }
    &__img{
      width: 100%;
      height: auto;
      &-wrap{
        position: relative;
        margin-top: 15px;
        top: unset;
        right: unset;
        svg{
          height: 34.5%;
          bottom: 13px;
          left: -21px;
        }
      }
    }
    &__desc{
      padding: 0 15px;
    }
    &__form{
      max-width: unset;
      margin: 0;
    }
  }

  .friends{
    &__nums{
      gap: 16px;
      padding: 18px 27px;
    }
    &__num{
      font-size: 94px;
      height: 123px;
    }
    &__title{
      font-size: 21px;
      margin-top: 23px;
    }
  }
}


@media screen and (max-width: 768px){
  .main{
    &__inner{
      padding: 0;
    }
    &__price{
      font-size: 70px;
    }
    &__desc{
      font-size: 25px;
    }
  }

  .donation{
    &__form{
      padding: 26px 15px 40px;
    }
    &__radios{
      margin-top: 14px;
      gap: 19px 16px;
    }
    &__inputs{
      margin-top: 30px;
      gap: 19px;
    }
  }

  .friends{
    padding: 74px 0 43px;
    &__inner{
      grid-template-columns: 100%;
      gap: 0;
    }
    &__right{
      order: -1;
    }
    &__left{
      margin-top: -55px;
      position: relative;
      z-index: 2;
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    &__heart{
      height: 61px;
    }
    &__price{
      font-size: 44px;
    }
    &__desc{
      font-size: 18px;
    }
  }

  .donation{
    &__radios{
      grid-template-columns: 1fr 1fr;
    }
    &__inputs{
      grid-template-columns: 1fr;
    }
    &__radio{
      &--text{
        input{
          font-size: 14px;
        }
      }
    }
  }
}
