@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import 'components/_fonts.scss';
.fg {
    position: relative;
    margin: 0;
    input {
        border: 1px solid #828282;
        padding: 0 20px;
        height: 38px;
        width: 100%;
        background-color: transparent;
        font-weight: 300;
        font-size: 12px;
        color: $black;
        &::placeholder {
            color: #828282;
        }
    }
}

.check {
    position: relative;
    input {
        position: absolute;
        opacity: 0;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        z-index: 2;
        cursor: pointer;
        &:checked+label {
            &::before {
                border-color: $primary;
                background-color: $primary;
            }
        }
        &:disabled+label {
            color: #AAAAAA;
            pointer-events: none;
        }
    }
    label {
        padding-left: 27px;
        position: relative;
        min-height: 20px;
        font-size: 16px;
        line-height: 134%;
        cursor: pointer;
        display: block;
        &:before {
            content: '';
            position: absolute;
            z-index: 1;
            border: 1px solid #828282;
            width: 20px;
            height: 20px;
            left: 0;
            top: 0;
        }
    }
    a{
        position: relative;
        z-index: 2;
        color: $primary;
    }
}
