.logo{
  width: 134px;
  flex-shrink: 0;
  display: block;
  img{
    width: 100%;
    display: block;
  }
}

.header{
  padding: 13px 0;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 3;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__btn{
    width: 147px;
    height: 40px;
    flex-shrink: 0;
    font-size: 14px;
  }
}


.nav{
  display: flex;
  align-items: center;
  &__link{
    font-size: 14px;
    line-height: 142%;
    text-transform: uppercase;
    &:not(:last-child){
      margin-right: 33px;
    }
  }
}

.burger{
  display: none;
}


@media screen and (max-width: 1100px){
  .nav{
    &__link{
      &:not(:last-child){
        margin-right: 20px;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .logo{
    width: 90px;
  }

  .nav{
    position: fixed;
    top: 69px;
    left: 0;
    width: 100%;
    height: calc(100vh - 69px);
    overflow-y: auto;
    padding: 15px 15px 30px;
    flex-direction: column;
    transform: translateX(-150%);
    transition: all .3s;
    background-color: #fff;
    &__link{
      font-size: 20px;
      &:not(:last-child){
        margin: 0 0 20px;
      }
    }
  }

  .burger{
    width: 32px;
    height: 18px;
    display: flex;
    align-items: center;
    position: relative;
    flex-shrink: 0;
    margin-right: 19px;
    span{
      display: block;
      width: 100%;
      height: 2px;
      background: $primary;
      border-radius: 2px;
      left: 0;
      transition: all .3s;
      &:nth-child(1){
        position: absolute;
        top: 0;
      }
      &:nth-child(3){
        position: absolute;
        bottom: 0;
        width: 22px;
      }
    }

  }

  .header{
    &__inner{
      justify-content: unset;
    }
    &__btn{
      margin-left: auto;
    }
    &._open{
      .burger{
        span{
          &:nth-child(1){
            transform: rotate(-45deg) translate(-7px,5px);
          }
          &:nth-child(2){
            transform: scale(0);
          }
          &:nth-child(3){
            width: 100%;
            transform: rotate(45deg) translate(-7px,-5px);
          }
        }
      }
      .nav{
        transform: translateX(0%);
      }
    }
  }
}
