.footer{
  border-top: 1px solid #E0E0E0;
  padding: 31px 0;
  &__inner{
    display: flex;
  }
  &__left{
    margin-right: 62px;
    flex-shrink: 0;
  }
  &__c{
    font-weight: 300;
    font-size: 12px;
    line-height: 142%;
    margin-top: 16px;
  }
  &__links{
    column-count: 2;
    column-gap: 20px;
    margin-right: 72px;
  }
  &__link{
    min-width: 133px;
    display: block;
    font-size: 14px;
    line-height: 142%;
    text-transform: uppercase;
    &:not(:last-child){
      margin-bottom: 14px;
    }
  }
  &__pay{
    &-title{
      font-size: 14px;
      line-height: 142%;
    }
    &-items{
      display: flex;
      align-items: center;
      margin-top: 5px;
    }
    &-item{
      display: flex;
      align-items: center;
      &:not(:last-child){
        margin-right: 17px;
      }
    }
  }
  &__right{
    margin-left: auto;
  }
  &__btn{
    height: 40px;
    width: 147px;
    font-size: 14px;
  }
  &__socials{
    display: flex;
    align-items: center;
    justify-content: flex-end;
    margin-top: 20px;
  }
  &__social{
    &:hover{
      img{
        transform: translateY(-10px);
      }
    }
    img{
      transition: all .3s;
    }
    &:not(:last-child){
      margin-right: 10px;
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    &__inner{
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 30px 10px;
    }
    &__links{
      margin: 0;
      grid-row: 1/3;
      grid-column: 2/3;
    }
    &__left{
      margin: 0;
    }
    &__right{
      margin: 0;
      grid-column: 1/3;
      display: flex;
      flex-direction: column;
      align-items: center;
    }
    &__socials{
      justify-content: center;
    }
  }
}


@media screen and (max-width: 768px){
  .footer{
    padding: 23px 0;
    &__links{
      column-count: 1;
    }
    &__link{
      min-width: unset;
    }
    &__pay{
      &-items{
        flex-wrap: wrap;
      }
      &-item{
        margin-bottom: 4px;
      }
    }
  }
}
