@import "../vars";
.mbtn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    &:focus {
        box-shadow: none;
    }
}

.mbtn-primary{
    background: linear-gradient(90deg, #F76F3E 0%, #E81771 100%);
    color: #fff;
    text-transform: uppercase;
    &:hover{
        color: #fff;
        opacity: .8;
    }
}
